button {
    border: none;
    background-color: rgp(80,60,209);
    font-size: 24px;
    color: rgp(255,255,255);
    font-weight: bold;
    cursor:pointer;
    border-radius: 10px;
    outline: none;
}

button:hover{
    background-color: rgp(61,43,184);
}

.equals{
    grid-column: 3 / 5;
    background-color: rgp(243,61,29);
}
.equals:hover{
    background-color: rgp(228,39,15);
}